.listButton {
  display: inline;
  margin: auto;
}

.listButton > button {
  padding: 2.5vmin;
  border-radius: 50%;
  border-style: solid;
  border-width: 2px;
  margin: 10px 10px;
}

.invisibleButton {
  border-color: transparent;
  background-color: transparent;
}

.unclickedButton {
  background-color: transparent;
  border-color: rgb(95, 95, 95);
  animation: unhoverColorChange 0.5s;
  animation-fill-mode: forwards;
}

.clickedButton {
  animation: clickColorChange 1s;
  animation-fill-mode: forwards;
}

@media (hover: hover) {
  .unclickedButton:hover {
    animation: hoverColorChange 0.3s;
    animation-fill-mode: forwards;
  }
}

@keyframes hoverColorChange {
  from {
    background-color: transparent;
  }
  to {
    background-color: rgb(58, 58, 58);
  }
}

@keyframes unhoverColorChange {
  from {
    background-color: rgb(58, 58, 58);
  }
  to {
    background-color: transparent;
  }
}

@keyframes clickColorChange {
  from {
    background-color: rgb(58, 58, 58);
    border-color: rgb(95, 95, 95);
  }
  to {
    background-color: rgba(245, 245, 245, 0.8);
    border-color: transparent;
  }
}
